/* eslint-disable no-param-reassign */
/* eslint-disable no-unused-vars */
/* eslint-disable react/no-array-index-key */
import React, { useState, useEffect } from 'react'
import { Box } from '@mui/material'
import qs from 'qs'
import SwiperCore, { Pagination, Navigation } from 'swiper'
import useStyles from './ContactsPage.style'
import api from '../../../api/api'
import useAsync from '../../../hooks/useAsync'
import { API_VACANCIES_CONTACTS_ENDPOINT } from '../../../utils/constants'
import Filters from '../../../components/Sub/Filters/Filters'
import DataTable from '../../../components/Sub/DataTable/DataTableWithServerPagination'
import useFilters from '../../../hooks/useFilters'
import contactsColumns from './columns'
import { NewContactPopup } from '../../../components/Sub/GlobalPopups'

SwiperCore.use([Pagination, Navigation])

const sortFieldsMapping = {
  first_name: 'first_name',
  last_name: 'last_name',
  position: 'position',
}

const ContactsPage = ({ setSnack }) => {
  const classes = useStyles()
  const hook = useFilters()
  const [filtersJSON, setFiltersJSON] = useState({})
  const [contactToEdit, setContactToEdit] = useState(undefined)
  const [rowsCount, setRowsCount] = useState(undefined)

  const { run, data, isLoading } = useAsync({
    data: [],
    error: null,
  })

  const [contacts, setContacts] = useState([])
  const [isOpenNewContactPopup, setIsOpenNewContactPopup] = useState(false)

  const paramsSerializer = (params) => qs.stringify(params, { arrayFormat: 'repeat' })

  const parseContactsRes = (contactsRes) =>
    contactsRes.map((contact) => ({
      ...contact,
      companies: contact?.vacancy?.map((company) => company.company_name),
      social_networks: contact.social_networks.map((socialNetwork) => socialNetwork.contact_info),
    }))

  const getContacts = async () => {
    try {
      const search = hook.filters?.search?.length > 2 ? hook.filters.search : undefined
      const response = await api.get(API_VACANCIES_CONTACTS_ENDPOINT, {
        params: { ...hook.filters },
        paramsSerializer,
      })
      const res = response.data.results
      setContacts(parseContactsRes(res))
      setRowsCount(response.data?.count)
      return Promise.resolve(res)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }

  React.useEffect(() => {
    if (!isOpenNewContactPopup && (hook.filters?.search?.length > 2 || !hook.filters?.search)) run(getContacts())
  }, [hook.filters, isOpenNewContactPopup])

  const columns = contactsColumns

  const handleOpenAddNewContactPopup = () => {
    setIsOpenNewContactPopup(true)
  }

  const handleBackClick = () => {
    setIsOpenNewContactPopup(false)
  }

  const onCellClick = (params, event) => {
    // eslint-disable-next-line no-param-reassign
    event.defaultMuiPrevented = true
    //     ,      
    //            
    //      
    const editContact = data?.find((contact) => contact?.id === params?.row?.id)
    setContactToEdit(editContact)
    handleOpenAddNewContactPopup()
  }

  return (
    <Box className={classes.root}>
      <NewContactPopup
        openState={[isOpenNewContactPopup, setIsOpenNewContactPopup]}
        setSnack={setSnack}
        onBackClick={handleBackClick}
        data={contactToEdit}
        clearData={() => setContactToEdit(undefined)}
      />
      <Filters
        filterType="contacts"
        hook={hook}
        filtersJSON={filtersJSON}
        handleAddContact={handleOpenAddNewContactPopup}
        contactsCount={rowsCount}
      />
      <DataTable
        columns={columns}
        rows={contacts}
        loading={isLoading}
        onCellClick={onCellClick}
        sx={{ '& .MuiDataGrid-cell': { cursor: 'pointer' } }}
        filtersHook={hook}
        rowsCount={rowsCount}
        sortFieldsMapping={sortFieldsMapping}
      />
    </Box>
  )
}

export default ContactsPage
