import * as React from 'react'
import { DataGrid, ruRU } from '@mui/x-data-grid'
import { createTheme, ThemeProvider } from '@mui/material/styles'
import { Stack } from '@mui/material'
import LoadingOverlay from './LoadingOverlay/LoadingOverlay'
import useStyles from './DataTable.style'

const theme = createTheme(ruRU)

export default function DataTableWithServerPagination({
  columns,
  rows,
  filtersHook,
  rowsCount,
  sortFieldsMapping,
  ...props
}) {
  const classes = useStyles()
  const { filters, changeFilters } = filtersHook

  const handleSortModelChange = (sortModel) => {
    if (sortModel.length === 0) {
      changeFilters({ ordering: undefined })
      return
    }
    const sortKeys = sortModel.map(
      (sortColumn) => `${sortColumn.sort === 'asc' ? '' : '-'}${sortFieldsMapping[sortColumn.field]}`,
    )
    changeFilters({ ordering: sortKeys.join(',') })
  }

  return (
    <ThemeProvider theme={theme}>
      <div style={{ height: 830, width: '100%' }}>
        <DataGrid
          className={classes.root}
          rows={rows}
          columns={columns}
          pageSize={10}
          rowCount={rowsCount}
          page={filters.page - 1}
          onPageChange={(newPage) => changeFilters({ page: newPage + 1 })}
          paginationMode="server"
          disableSelectionOnClick
          disableColumnMenu
          disableColumnSelector
          rowHeight={64}
          sortingMode="server"
          onSortModelChange={handleSortModelChange}
          {...props}
          components={{
            NoRowsOverlay: () => (
              <Stack height="100%" alignItems="center" justifyContent="center">
                 
              </Stack>
            ),
            NoResultsOverlay: () => (
              <Stack height="100%" alignItems="center" justifyContent="center">
                Local filter returns no result
              </Stack>
            ),
            ErrorOverlay: () => (
              <Stack height="100%" alignItems="center" justifyContent="center">
                Error
              </Stack>
            ),
            LoadingOverlay,
          }}
        />
      </div>
    </ThemeProvider>
  )
}
